import Vue from "vue";
import Router from "vue-router";
import BoardList from "@/components/BoardList";
import ShowBoard from "@/components/ShowBoard";
import EditBoard from "@/components/EditBoard";
import Home from "@/views/Home";
import Venue from "@/views/Venue";
import RSVP from "@/views/RSVP";
import Login from "@/views/Login";
import FAQs from "@/views/FAQs";
import TheTeam from "@/views/TheTeam";
import ShowForm from "@/components/ShowForm";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "*",
      redirect: "/"
    },
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/venue",
      name: "Venue",
      component: Venue
    },
    {
      path: "/login",
      name: "Login",
      component: Login
    },
    {
      path: "/RSVP",
      name: "RSVP",
      component: RSVP
      // props: true,
      // beforeEnter: (to, from, next) => {
      //   if (to.params.email) {
      //     next();
      //   } else {
      //     next({ name: "Login" });
      //   }
      // }
    },
    {
      path: "/faqs",
      name: "FAQs",
      component: FAQs,
      meta: {
        title: "FAQs"
      }
    },
    {
      path: "/theteam",
      name: "The Team",
      component: TheTeam,
      meta: {
        title: "The Team"
      }
    },
    {
      path: "/show-form/:id",
      name: "ShowForm",
      component: ShowForm,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/show-board/:id",
      name: "ShowBoard",
      component: ShowBoard
    },
    {
      path: "/",
      name: "BoardList",
      component: BoardList
    },
    {
      path: "/edit-board/:id",
      name: "EditBoard",
      component: EditBoard
    }
  ]
});
